.form-demo .card {
    min-width: 650px;
    height: 700px;
    padding: 160px;

}

.form-demo .card form {
    margin-top: 3rem;
}

.form-demo .card .field {
    margin-bottom: 4rem;
}

@media screen and (max-width: 960px) {
    .form-demo .card {
        width: 80%;
    }
}

@media screen and (max-width: 640px) {
    .form-demo .card {
        width: 100%;
        min-width: 0;
    }
}

