.p-inputtext:enabled:focus {
    outline: 0 none;
    outline-offset: 0;
    box-shadow: 0 0 0 0.1rem rgb(255, 208, 216);
    border-color: magenta;
}

.p-button:enabled:active,
.p-button:not(button):not(a):not(.p-disabled):active {
    background: pink;
    color: #ffffff;
    border-color: pink;
}

.p-button:enabled:hover,
.p-button:not(button):not(a):not(.p-disabled):hover {
    background: pink;
    color: #ffffff;
    border-color: pink;
}

.p-tabview .p-tabview-nav-btn.p-link:focus {
    outline: 0 none;
    outline-offset: 0;
    box-shadow: inset 0 0 0 0.2rem #00fbff;
}

.p-button:focus {
    box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px white, 0 1px 2px 0 black;
}



